    <div class="container card">
      <ul class="listBookmarks">
        <li
          v-for="item in computedTabs"
          :key="item"
          :class="['bookmarkBtn', { active: currentTab === item}]"
          @click="currentTab = item"
        >
          {{ item }}
        </li>
      </ul>
      <div class="listBody pr">
        <div class="card pr p10" v-if="computedListValue">
        <textarea
          class="rareArea"
          v-model="computedListValue"
        />
          <button @click="save" class="saveBtn hoverNegative">Save manual fixes</button>
        </div>
      </div>
    </div>

  </section>
