<script>
  import SubTitle from '../../components/SubTitle';
  import ServicePanel from '../../components/ServicePanel';
  export default {
    components: { SubTitle, ServicePanel },
    data() {
      return {
        currentTab:Object.keys(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'])[0],
        localList:[]
      };
    },
    computed: {
      computedTabs(){
        return Object.keys(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'])
      },
      computedListValue:{//Стейт данные определенной закладки. Двунаправленное вычисляемое св-во
        get() {
          return JSON.stringify(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.currentTab])
        },
        set(value) {
          // console.log('set', value)
          this.localList = JSON.parse(value)
        }
      }
    },
    methods: {
      save(){
        if(this.computedListValue.length!==0){
          this.$store.commit('setRare',{
            list:this.localList,
            listName:this.currentTab,
          })
        }
      },
    },
  };
</script>

