<script>


  export default {
    data() {
      return {
        radio: true,
        dataToSetLocal: '',
        urlStr:''
      };
    },
    computed:{
      computedDataToSet:{
        get() {
          return this.dataToSetLocal
        },
        set(value) {
          if(value.length<100){
            alert('Нет списка. Игнор')
            this.dataToSetLocal='';
            return
          }
          // console.log('set', value)
          const parsedValue = JSON.parse(value);//превращено из текста в obj структуру
          const data = parsedValue.infoMain;
          this.dataToSetLocal = data

          if(!data.tradeList && !data.tradeList.length){
            alert('Нет трейд списка. Игнор')
            this.dataToSetLocal='';
            return
          }
          this.$store.dispatch('recordManualDataToFB', data);
        }
      },
      computedFonUrlToSet: {
        get() {
          return this.urlStr
        },
        set(value) {
          if(value.length<20){
            alert('Нет url. Игнор')
            this.urlStr='';
            return
          }
          this.$store.commit('setService', { name:'fonUrl', value:'url(' + value + ')' })
          // this.$store.commit('activatePopupFB',{text:'fon successfully set'});
          // this.$store.dispatch('updateFB',{name:'service',info:this.$store.state.isAK?'infoAK':'infoMain'});
        }
      },
      computedDataToCopy(){
        return JSON.stringify({
          infoMain:this.$store.state.infoMain||[],
          infoAK:this.$store.state.infoAK||[],
        })
      },
    },
